*{

    box-sizing: border-box;
}

body{
    font-family: "Roboto", sans-serif;
}

nav{
   
    padding-left: 8rem;
    padding-right: 8rem;

box-shadow: 0px 4px 5px -2px rgba(0, 0, 0, 0.2);
    background-color: rgb(251, 251, 251);
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    gap: 1 rem;    
    margin-right: 10px;
  


  background-color: #ffffff;

  position: fixed;

  top: 0;
  left: 0;
  /* margin-top: -11%;
/*   
  
  */
  z-index: 999; 
  width: 100%; 
  font-family: "Roboto", sans-serif;
  font-weight: 550;
  font-style: normal;
  display: flex;
  align-items: center;
  
}

nav ul{


    list-style-type: none;
    display: flex;
    gap:2rem;
    padding-top: 15px;
    padding-left: 20px;
    padding-right: 20px;
    color: rgb(145,145,145);
}



nav a{

    text-decoration: none;
    color: rgb(80, 79, 79);
    
    padding-right: 20px;
        

}

.nav ul:active {
    color: orangered;

}

.nav li:hover{
color: orangered;


}

.schedule{

 
    margin-top: -5%;
    background-color: black;
    color: white;

    height: 38px;

    padding-right: 20px;
    border-radius: 200px;
    margin-bottom: -1rem;
  
    display: flex;
    align-items: center;
    
}

.schedule-image{
    background-color: rgb(243,85,37);
    padding: 12px;
    margin-top: 10%;
    border-radius: 50%;
    font-weight: 200;
}

.schedule p{
    padding-bottom:20px;
    margin-bottom: 0%;
    font-weight: 300;
}