.accordion {
  display: flex;
  flex-direction: column;
  width: 500px;
  padding: 20px;
  border-radius: 5px;
}

.question {
  padding: 10px;
  font-weight: bold;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
}

.answer {
  display: none;
  padding: 10px;
}

.answer.open {
  display: block;
}


.property-buttons {
  display: flex;
  justify-content: space-around;
  padding: 10px;
  margin-bottom: 20px; /* Adjust spacing as needed */
}

.property-button {
  background-color: black;
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  margin-left: 1rem;
  font-size: larger;

}

.property-button:hover {
  background-color: orange; /* Change active color if desired */
}

.round-buttons {
  display: flex;
  justify-content: center;
  
}

.round-button {
  background-color: black;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 50%; 
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  margin-left: 1rem;
  margin-bottom: 10rem;
}


.round-button:hover {
  background-color: orange;
}

.round-button:active {
  background-color: darkorange;
}

